body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.root {
  flex-grow: 1;
  background-color: #1e1e1e;
  z-index: 500;
  position: fixed;
  width: 100%;
  top: 0;
}

.grow {
  flex-grow: 1;
  font-weight: bold;
}

.menuButton {
  margin-left: -12;
  margin-right: 20;
}

html,
body {
  width: 100%;
  height: 100%;
  display: grid;
  background-color: #eeeeee;
  scroll-behavior: smooth;
}

h1 {
  font-weight: lighter;
  font-size: 4rem;
  margin: 1.5rem;
  cursor: default;
  text-align: center;
  margin-left: 3rem;
  margin-right: 3rem;
}
h3 {
  text-align: center;
  margin: 1.5rem;
  font-weight: lighter;
  grid-column: 3 / end;
  cursor: pointer;
  font-size: 1.5rem;
  margin-left: 3rem;
  margin-right: 3rem;
}

p,
small {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-weight: lighter;
  cursor: default;
}

ul {
  margin-left: -4rem;
}

.HomeContainer {
  padding-top: 3rem;
}

.bigAvatar {
  height: 10rem;
  width: 10rem;
  border-radius: 100px;
  margin: auto;
}

.headerContainer {
  display: grid;
  background-image: url(/static/media/headerImg.76b47216.jpg);
  padding-top: 3rem;
  padding-bottom: 4rem;
}

.headerTextContainer {
  padding: 0;
  margin: 0;
}

.homeHeader {
  text-align: center;
  font-weight: lighter;
  color: aliceblue;
  padding: 0;
  margin: 12px;
  cursor: default;
}

.aboutTextContainer {
  text-align: justify;
  margin-left: 3rem;
  margin-right: 3rem;
}

.aboutLanguages {
  font-style: italic;
}

.PortfolioContainer {
  display: grid;
}

.portfolioTextContainer {
  margin-left: 3rem;
  margin-right: 3rem;
}

.aLink {
  color: black;
  text-decoration: none;
}

.ContactContainer {
  display: grid;
  background-color: #9fe7e8;
}

.contactTextContainer {
  margin-left: 3rem;
  margin-right: 3rem;
}

.FooterContainer {
  width: 100%;
  background-color: rgba(27, 27, 27, 0.9);
  padding-top: 2px;
  padding-bottom: 2rem;
  bottom: 0;
  left: 0;
  height: 3rem;
  position: relative;
  text-align: center;
  vertical-align: baseline;
  text-transform: uppercase;
  color: aliceblue;
}

